
.statistics{
    display: flex;
    flex-direction: column;
    align-items: center;
    width: 30%;
    margin-top: 30px;
    box-shadow: rgba(0, 0, 0, 0.35) 0px 5px 15px;
    border-radius: 0.5rem;
}

.title{
margin: 10px 0 10px;
}

.stat_list{
    width: 100%;
    display: grid;
    grid-template-columns: repeat(5, 1fr); 
}


.stat_list > .itemLiTitle,
.stat_list > .itemLiNotTitle{
    display: flex;
    flex-direction: column;
    align-items: center;
    padding: 10px 0 10px;
}


.stat_list >.itemLiNotTitle:first-child{
    border-top-left-radius: 0.5em;
    border-bottom-left-radius: 0.5em;
}


.stat_list >.itemLiNotTitle:last-child{
    border-top-right-radius: 0.5em;
    border-bottom-right-radius: 0.5em;
}


.stat_list >.itemLiTitle:first-child{
    border-bottom-left-radius: 0.5em;
}


.stat_list >.itemLiTitle:last-child{
    border-bottom-right-radius: 0.5em;
}


.percentage{
    font-size: 22px;
    font-weight: 600;
}