.profile {
  min-width: 20%;
  margin-top: 20px;
  height: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
  box-shadow: rgba(0, 0, 0, 0.35) 0px 5px 15px;
  border-radius: 0.5rem;
}
.description {
  display: flex;
  flex-direction: column;
  align-items: center;
}
.avatar {
  margin-top: 30px;
  width: 200px;
  border: 6px double rgb(60, 44, 44);
  border-radius: 50%;
}
.description > p {
  margin-top: 20px;
}
.name {
  font-size: 40px;
  font-weight: 700;
}
.tag,
.location {
  font-size: 24px;
  color: #9a9a9d;
}
.stats {
  width: 100%;
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  margin-top: 30px;
}

.stats > li {
  display: flex;
  flex-direction: column;
  align-items: center;
  border-top: 1px solid #d3d5e3;
  background-color: rgba(241, 241, 241, 0.833);
  padding: 20px 0 20px;
}

.middleLi {
  border-right: 1px solid #d3d5e3;
  border-left: 1px solid #d3d5e3;
}

.label,
.quantity {
  font-size: 15px;
}

.quantity {
  font-weight: 700;
}

.stats > li:first-child {
  border-bottom-left-radius: 0.5em;
}

.stats > li:last-child {
  border-bottom-right-radius: 0.5em;
}
